<template>
    <div>
    <ul>
      <router-link to="/businessMain"><li :style="{'color':colorChange1}"><img :src="icon1" alt="">首页</li></router-link>
      <router-link to="/businessInformation"><li :style="{'color':colorChange2}"><img :src="icon2" alt="">消息</li></router-link>
      <router-link to="/businessCart"><li :style="{'color':colorChange3}"><img :src="icon3" alt="" :class="{size:isSmall}">订单</li></router-link>
    </ul>
    </div>
</template>



<script>
import house from '@/assets/img/主界面/资源 36.png'
import houseRed from '@/assets/img/主界面/资源 8.png'
import information from '@/assets/img/主界面/资源 9.png'
import informationRed from '@/assets/img/主界面/资源 19.png'
import cart from '@/assets/img/主界面/资源 10.png'
import cartRed from '@/assets/img/主界面/cartRed.png'
export default {
    data(){
        return{
      colorChange1:'grey',
      colorChange2:'grey',
      colorChange3:'grey',
      icon1:house,
      icon2:information,
      icon3:cart,
      isSmall:false
        }
    },
    methods:{

    },
  watch: {
    $route: {
      immediate: true, // 一旦监听到路由的变化立即执行
      handler(route) {
          if(route.path=='/businessMain')
          {
            this.icon1=houseRed
            this.colorChange1='black'              
          }
          else{
              this.icon1=house
              this.colorChange1='grey'
          }
            if(route.path=='/businessInformation')
          {
            this.icon2=informationRed
            this.colorChange2='black'              
          }
          else{
              this.icon2=information
              this.colorChange2='grey'
          }
                    if(route.path=='/businessCart')
          {
            this.icon3=cartRed
            this.isSmall=true
            this.colorChange3='black'              
          }
          else{
              this.icon3=cart
              this.colorChange3='grey'
          }
      },
    },
  }
}
</script>


<style lang="scss" scoped>
ul{
  height: 100px;
  background-color: rgb(247, 245, 241);
  align-items: center;
  position: fixed;
  bottom: 0%;
  left: 0;
  right: 0;
  justify-content: space-around;
  display: flex;
  li{
    width: 70px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    font-size: 30px;/*px*/
    img{
      padding-top: 10px;
      width: 50px;
      height: 50px;
      padding-bottom: 5px;
    }
    .size{
        width: 70px;
        height: 70px;
    }
  }
}
</style>
